/* App.css */

body {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  background: linear-gradient(180deg, #f7f9fc 0%, #d5e4fb 100%);
  min-height: 100vh;
  overflow-x: hidden;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 50px;
}

/* Background abstract ball */
.background-ball {
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  filter: blur(80px);
  z-index: -1;
  transform: translateX(-50%);
}

.main-content {
  max-width: 1000px;
  padding: 0 20px;
  text-align: center;
}

.sleek-title {
  font-size: 36px;
  color: #1f2e48;
  font-weight: 600;
}

.sleek-image {
  width: 100%;
  max-width: 800px;
  border-radius: 12px;
  margin-top: 20px;
  transition: transform 0.3s ease-in-out;
}

.sleek-image:hover {
  transform: scale(1.02);
  /* Slight hover effect */
}

.sleek-content {
  font-size: 16px;
  color: #444444;
  line-height: 1.75;
  margin-bottom: 20px;
  text-align: justify;
}

.sleek-subtitle {
  font-size: 22px;
  color: #333333;
  font-weight: 600;
  margin-top: 30px;
}